import auth0 from 'auth0-js'
import Cookies from 'js-cookie'
import jwt from 'jsonwebtoken'

class Auth0 {  
  constructor() {
    this.auth0 = new auth0.WebAuth({
        domain: 'marvelous-studio.auth0.com',
        clientID: 'sHsD76ZBw7vm1Wnte0LnAz4SZ28vVYdE',
        redirectUri: 'http://localhost:3000/callback',
        responseType: 'token id_token',
        scope: 'openid profile'
    });

    this.login = this.login.bind(this)
    this.logout = this.logout.bind(this)
    this.isAuthenticated = this.isAuthenticated.bind(this);
    this.handleAuthentication = this.handleAuthentication.bind(this)
  }

  login() {
    this.auth0.authorize();
  }

  handleAuthentication() {
    return new Promise((resolve, reject) => {
      this.auth0.parseHash((err, authResult) => {
        if (authResult && authResult.accessToken && authResult.idToken) {
          this.setSession(authResult);        
          resolve()
        } else if (err) {                  
          console.log(err);
          alert(`Error: ${err.error}. Check the console for further details.`);
          reject()
        }
      })
    })  
  }
  
  setSession(authResult) {        
    const expiresAt = (authResult.expiresIn * 1000) + new Date().getTime();    
    Cookies.set('user', authResult.idTokenPayLoad)
    Cookies.set('jwt', authResult.idToken)
    Cookies.set('expiresAt', expiresAt)
  }

  logout() {
    Cookies.remove('user')
    Cookies.remove('jwt')
    Cookies.remove('expiresAt')
    this.auth0.logout({
      returnTo: '',
      clientID: 'sHsD76ZBw7vm1Wnte0LnAz4SZ28vVYdE'
    })
  }

  isAuthenticated() {
    const expiresAt = Cookies.getJSON('expiresAt')
    return new Date().getTime() < expiresAt
  }

  verifyToken(token) {
    if(token){
      const decodeToken = jwt.decode(token)
      const expiresAt = decodeToken.exp * 1000
      return (decodeToken && new Date().getTime() < expiresAt) ? decodeToken : undefined
    }
    return undefined
  }

  clientAuth() {
    const token = Cookies.getJSON('jwt')
    const verifyToken = this.verifyToken(token)
    return verifyToken
  }  

  serverAuth(req) {    
    if(req.headers.cookie){
      const jwtCookie = req.headers.cookie.split(';').find(c => c.trim().startsWith('jwt='))
      if(!jwtCookie){ return undefined }
      const token = jwtCookie.split('=')[1]
      const verifyToken = this.verifyToken(token)
      return verifyToken
    }
  }
}

const auth0Client = new Auth0()
export default auth0Client